"use strict";

const App = (function (Navigation, ImageSlider) {

  /**** wird vor dem DOM ready ausgefhrt ****/

  /**** wird nach dem DOM ready ausgefhrt ****/
  function init(){
    console.log('init() of App called');
    Navigation.init();
    ImageSlider.init();


  };

  //public api
  return{
    init: init
  }

})(Navigation, ImageSlider);

//wenn der DOM vollstndig geladen ist init aufrufen
domIsReady(App.init);
